import { useState, useEffect, useRef } from 'react'

export default function Board() {
  let [squareOne, setSquareOne] = useState(null)
  let [squareTwo, setSquareTwo] = useState(null)
  let [squareThree, setSquareThree] = useState(null)
  let [squareFour, setSquareFour] = useState(null)
  let [squareFive, setSquareFive] = useState(null)
  let [squareSix, setSquareSix] = useState(null)
  let [squareSeven, setSquareSeven] = useState(null)
  let [squareEight, setSquareEight] = useState(null)
  let [squareNine, setSquareNine] = useState(null)
  let [booleanPlayer, setBooleanPlayer] = useState(true)
  let one = useRef()
  let two = useRef()
  let three = useRef()
  let four = useRef()
  let five = useRef()
  let six = useRef()
  let seven = useRef()
  let eight = useRef()
  let nine = useRef()
  let currentPlayer = 'X'

  // checking to see if player wins using ref
  useEffect(() => {
    if (booleanPlayer) {
      setBooleanPlayer(false)
    } else {
      setBooleanPlayer(true)
    }
    if (squareOne !== null && squareTwo !== null && squareThree !== null) {
      if (
        one.current.innerHTML === two.current.innerHTML &&
        one.current.innerHTML === three.current.innerHTML
      ) {
        alert(`${currentPlayer} wins!`)
        window.location.reload()
      }
    }
    if (squareFour !== null && squareFive !== null && squareSix !== null) {
      if (
        four.current.innerHTML === five.current.innerHTML &&
        four.current.innerHTML === six.current.innerHTML
      ) {
        alert(`${currentPlayer} wins!`)
        window.location.reload()
      }
    }
    if (squareSeven !== null && squareEight !== null && squareNine !== null) {
      if (
        seven.current.innerHTML === eight.current.innerHTML &&
        seven.current.innerHTML === nine.current.innerHTML
      ) {
        alert(`${currentPlayer} wins!`)
        window.location.reload()
      }
    }
    if (squareOne !== null && squareFour !== null && squareSeven !== null) {
      console.log(
        one.current.innerHTML,
        four.current.innerHTML,
        seven.current.innerHTML
      )
      if (
        one.current.innerHTML === four.current.innerHTML &&
        one.current.innerHTML === seven.current.innerHTML
      ) {
        alert(`${currentPlayer} wins!`)
        window.location.reload()
      }
    }
    if (squareTwo !== null && squareFive !== null && squareEight !== null) {
      if (
        two.current.innerHTML === five.current.innerHTML &&
        two.current.innerHTML === eight.current.innerHTML
      ) {
        alert(`${currentPlayer} wins!`)
        window.location.reload()
      }
    }
    if (squareThree !== null && squareSix !== null && squareNine !== null) {
      if (
        three.current.innerHTML === six.current.innerHTML &&
        three.current.innerHTML === nine.current.innerHTML
      ) {
        alert(`${currentPlayer} wins!`)
        window.location.reload()
      }
    }
    if (squareOne !== null && squareFive !== null && squareNine !== null) {
      if (
        one.current.innerHTML === five.current.innerHTML &&
        one.current.innerHTML === nine.current.innerHTML
      ) {
        alert(`${currentPlayer} wins!`)
        window.location.reload()
      }
    }
    if (squareThree !== null && squareFive !== null && squareSeven !== null) {
      if (
        three.current.innerHTML === five.current.innerHTML &&
        three.current.innerHTML === seven.current.innerHTML
      ) {
        alert(`${currentPlayer} wins!`)
        window.location.reload()
      }
    }
  }, [
    squareOne,
    squareTwo,
    squareThree,
    squareFour,
    squareFive,
    squareSix,
    squareSeven,
    squareEight,
    squareNine
  ])

  function handleClick(squareNumber) {
    if (booleanPlayer) {
      currentPlayer = 'O'
    } else {
      currentPlayer = 'X'
    }
    if (squareNumber === 1) {
      if (squareOne === null) {
        setSquareOne(currentPlayer)
      }
    } else if (squareNumber === 2) {
      if (squareTwo === null) {
        setSquareTwo(currentPlayer)
      }
    } else if (squareNumber === 3) {
      if (squareThree === null) {
        setSquareThree(currentPlayer)
      }
    } else if (squareNumber === 4) {
      if (squareFour === null) {
        setSquareFour(currentPlayer)
      }
    } else if (squareNumber === 5) {
      if (squareFive === null) {
        setSquareFive(currentPlayer)
      }
    } else if (squareNumber === 6) {
      if (squareSix === null) {
        setSquareSix(currentPlayer)
      }
    } else if (squareNumber === 7) {
      if (squareSeven === null) {
        setSquareSeven(currentPlayer)
      }
    } else if (squareNumber === 8) {
      if (squareEight === null) {
        setSquareEight(currentPlayer)
      }
    } else if (squareNumber === 9) {
      if (squareNine === null) {
        setSquareNine(currentPlayer)
      }
    }
  }

  return (
    <div id='board-container'>
      <table>
        <tbody>
          <tr>
            <td
              onClick={() => {
                handleClick(1)
              }}
              ref={one}
            >
              {squareOne}
            </td>
            <td
              onClick={() => {
                handleClick(2)
              }}
              ref={two}
            >
              {squareTwo}
            </td>
            <td
              onClick={() => {
                handleClick(3)
              }}
              ref={three}
            >
              {squareThree}
            </td>
          </tr>
        </tbody>
        <tbody>
          <tr>
            <td
              onClick={() => {
                handleClick(4)
              }}
              ref={four}
            >
              {squareFour}
            </td>
            <td
              onClick={() => {
                handleClick(5)
              }}
              ref={five}
            >
              {squareFive}
            </td>
            <td
              onClick={() => {
                handleClick(6)
              }}
              ref={six}
            >
              {squareSix}
            </td>
          </tr>
        </tbody>
        <tbody>
          <tr>
            <td
              onClick={() => {
                handleClick(7)
              }}
              ref={seven}
            >
              {squareSeven}
            </td>
            <td
              onClick={() => {
                handleClick(8)
              }}
              ref={eight}
            >
              {squareEight}
            </td>
            <td
              onClick={() => {
                handleClick(9)
              }}
              ref={nine}
            >
              {squareNine}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  )
}
